.modal {
  position: fixed;
  top: 0;
  left: 0;
  z-index: 999;
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  background-color: rgba(#000, 0.5);
  cursor: pointer;

  &__inner {
    position: relative;
    width: 85%;
    max-width: vw(400);
    align-self: center;
    padding: vw(40);
    background-color: #ffffff;
    cursor: default;

    @include max(sm) {
      max-width: vw(400, 414);
      padding: vw(40, 414);
      font-size: vw(18, 414);
    }

    @include min(xxl) {
      max-width: 400px;
      padding: 40px;
      font-size: 20px;
    }
  }

  &__close {
    position: absolute;
    top: vw(12);
    right: vw(12);
    padding: 0;
    line-height: 1;
    border: none;
    background-color: transparent;
    cursor: pointer;

    appearance: none;

    @include max(sm) {
      top: vw(12, 414);
      right: vw(12, 414);
    }

    @include min(xxl) {
      top: 12px;
      right: 12px;
    }
  }
}
